.card-container{
    margin: 10px;
    height: 150px;
    min-width: 100px;
    width: 10%;
    background-color: gray;
    border: 6px solid grey;
    border-radius: 25px;
    display: inline-block
}

@media (max-width: 700px) {
    .card-container {
        margin: 5px;
        height: 60px;
        width: 40px;
        min-width: 10px;
        border-radius: 10px;
        border: 2px solid grey;
    }
}


